// SCSS variables are information about icon's compiled state, stored under its original file name
//
// .icon-home {
//   width: $icon-home-width;
// }
//
// The large array-like variables contain all information about a single icon
// $icon-home: x y offset_x offset_y width height total_width total_height image_path;
//
// At the bottom of this section, we provide information about the spritesheet itself
// $spritesheet: width height image $spritesheet-sprites;
$datepicker-name: 'datepicker';
$datepicker-x: 50px;
$datepicker-y: 0px;
$datepicker-offset-x: -50px;
$datepicker-offset-y: 0px;
$datepicker-width: 23px;
$datepicker-height: 18px;
$datepicker-total-width: 73px;
$datepicker-total-height: 59px;
$datepicker-image: '../images/icons/pc-icons.png';
$datepicker: (50px, 0px, -50px, 0px, 23px, 18px, 73px, 59px, '../images/icons/pc-icons.png', 'datepicker', );
$form-minus-name: 'form-minus';
$form-minus-x: 30px;
$form-minus-y: 49px;
$form-minus-offset-x: -30px;
$form-minus-offset-y: -49px;
$form-minus-width: 10px;
$form-minus-height: 2px;
$form-minus-total-width: 73px;
$form-minus-total-height: 59px;
$form-minus-image: '../images/icons/pc-icons.png';
$form-minus: (30px, 49px, -30px, -49px, 10px, 2px, 73px, 59px, '../images/icons/pc-icons.png', 'form-minus', );
$form-plus-name: 'form-plus';
$form-plus-x: 0px;
$form-plus-y: 49px;
$form-plus-offset-x: 0px;
$form-plus-offset-y: -49px;
$form-plus-width: 10px;
$form-plus-height: 10px;
$form-plus-total-width: 73px;
$form-plus-total-height: 59px;
$form-plus-image: '../images/icons/pc-icons.png';
$form-plus: (0px, 49px, 0px, -49px, 10px, 10px, 73px, 59px, '../images/icons/pc-icons.png', 'form-plus', );
$layer-close-name: 'layer-close';
$layer-close-x: 0px;
$layer-close-y: 0px;
$layer-close-offset-x: 0px;
$layer-close-offset-y: 0px;
$layer-close-width: 30px;
$layer-close-height: 29px;
$layer-close-total-width: 73px;
$layer-close-total-height: 59px;
$layer-close-image: '../images/icons/pc-icons.png';
$layer-close: (0px, 0px, 0px, 0px, 30px, 29px, 73px, 59px, '../images/icons/pc-icons.png', 'layer-close', );
$spritesheet-width: 73px;
$spritesheet-height: 59px;
$spritesheet-image: '../images/icons/pc-icons.png';
$spritesheet-sprites: ($datepicker, $form-minus, $form-plus, $layer-close, );
$spritesheet: (73px, 59px, '../images/icons/pc-icons.png', $spritesheet-sprites, );

// The provided mixins are intended to be used with the array-like variables
//
// .icon-home {
//   @include sprite-width($icon-home);
// }
//
// .icon-email {
//   @include sprite($icon-email);
// }
//
// Example usage in HTML:
//
// `display: block` sprite:
// <div class="icon-home"></div>
//
// To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:
//
// // CSS
// .icon {
//   display: inline-block;
// }
//
// // HTML
// <i class="icon icon-home"></i>
@mixin sprite-width($sprite) {
  width: nth($sprite, 5);
}

@mixin sprite-height($sprite) {
  height: nth($sprite, 6);
}

@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 3);
  $sprite-offset-y: nth($sprite, 4);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite-image($sprite) {
  $sprite-image: nth($sprite, 9);
  background-image: url(#{$sprite-image});
}

@mixin sprite($sprite) {
  @include sprite-image($sprite);
  @include sprite-position($sprite);
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}

// The `sprites` mixin generates identical output to the CSS template
//   but can be overridden inside of SCSS
//
// @include sprites($spritesheet-sprites);
@mixin sprites($sprites) {
  @each $sprite in $sprites {
    $sprite-name: nth($sprite, 10);
    .#{$sprite-name} {
      @include sprite($sprite);
    }
  }
}
