@import "../component.scss";

.welcome-txt{
	padding-top: 35px;
	color: #333;
	font-size: 24px;
	line-height: 1.75;
	text-align: center;
}

.search-period{
	margin-left: 15px;
	.btn{
		width: 101px;
	}
}

.mypage-summary{
	@include clear;
	li{
		position: relative;
		float: left;
		width: 33.333%;
		margin: 25px 0;
		padding-right: 50px;
		text-align: right;
		font-size: 16px;
		color: #333;
		box-sizing: border-box;
		&:before{
			content: '';
			position: absolute;
			top: 50%;
			left: 0;
			display: inline-block;
			width: 1px;
			height: 80%;
			background-color: #f2f2f2;
			transform: translateY(-50%);
		}
		&:first-child{
			&:before{
				display: none;
			}
		}
		.title{
			line-height: 1.5;
		}
		.count{
			padding: 10px 0;
			letter-spacing: -.5px;
			font-size: 32px;
			strong{
				font-size: 36px;
				font-weight: 700;
			}
		}
		.detail{
			span{
				display: block;
			}
		}
	}
}

.my-program{
	.tab-btns{
		margin-bottom: 12px;
		.btn{
			width: 145px;
		}
	}
	.program-wrap{
		padding: 30px;
		border-top: 1px solid #000;
		border-bottom: 1px solid #ccc;
		background-color: #fff;
	}
	.table-horizontal {
		.th {
			width: 233px;
			time {
				color: #888;
				font-weight: 400;
				display: block;
			}
		}
		.same-form {
			background: {
				color: #fcfcfc;
			}
		}
	}

	.same-data ~ .multi-list{
		margin-top: 12px;
	}

	.add-time-data {
		li {
			margin: {
				top: 20px;
			}
		}
		time {
			color: #717171;
			font-size: 14px;
		}
		.title {
			display: block;
			margin: {
				bottom: 8px;
			}
		}
		.btn {
			line-height: 0;
			width: 26px;
			height: 26px;
			padding: 0;
		}
	}

	.program-content {
		color: #888;
		margin-bottom: 100px;
		padding: 46px 30px;
		border-bottom: 1px solid #ccc;
	}

	.board-detail{
		border-top: 1px solid #000;
		.detail-summary{
			padding-top: 35px;
			padding-bottom: 35px;
		}
	}

}

.multi-list{
	.btn {
		padding: {
			right: 20px;
			left: 20px;
		};
	}
}

.partner-list {
	ul{
		padding-top: 21px;
		padding-bottom: 15px;
	}
	li{
		display: table;
		width: 100%;
		font-size: 14px;
		strong,
		div{
			display: table-cell;
			box-sizing: border-box;
		}
		strong{
			width: 26.47%;
			padding: {
				top: 10px;
				bottom: 10px;
				left: 20px;
			}
			font-weight: 700;
			letter-spacing: -0.5px;
		}
		div{
			vertical-align: middle;
			font-size: 14px;
			color: #888;
			font-weight: 700;
			padding: {
				top: 10px;
				bottom: 10px;
				left: 20px;
			}
		}
	}
}

.schedule{
	font-size: 14px;
	&-name{
		margin-bottom: 5px;
		font-weight: 700;
		color: #000;
	}
	&-list{
		line-height: 1.4;
		color: #666;
	}
}

.activity-time {
	.input-box {
		width: 150px;
		&.datepicker {
			position: relative;
			margin-right: 26px;
			&:after {
				content: '';
				position: absolute;
				top: 50%;
				right: 11px;
				display: block;
				margin-top: $datepicker-height / -2;
				@include sprite($datepicker);
			}
		}
	}
	.help {
		margin: {
			right: 16px;
		}
	}
	.btn {
		line-height: 0;
		vertical-align: middle;
		width: 26px;
		height: 26px;
		padding: 0;
	}
}

.vms{
	&-introduce{
		& > .inner{
			position: relative;
			max-width: 880px;
			margin: 0 auto;
			border-top: 1px solid #000;
		}
		h3{
			margin-bottom: 25px;
			padding-top: 35px;
			font-weight: 700;
			font-size: 24px;
			color: #333;
		}
		.con{
			margin-bottom: 35px;
			line-height: 1.5;
			color: #333;
		}
	}
	&-join{
		background-color: #fcfcfc;
		& > .inner{
			position: relative;
			max-width: 880px;
			margin: 0 auto;
			padding: {
				top: 45px;
				bottom: 48px;
			}
		}
		h4{
			margin-bottom: 15px;
			padding: 0;
			color: #333;
			font-weight: 700;
			font-size: 20px;
		}
		.con{
			margin-bottom: 35px;
			line-height: 1.5;
			color: #333;
			.link{
				text-decoration: underline;
			}
		}
		.howto{
			@include clear;
			& > p{
				float: left;
				width: 148px;
				height: 148px;
				padding: 0 20px;
				border: {
					width: 8px;
					style: solid;
					radius: 50%;
				}
				text-align: center;
				box-sizing: border-box;
				em{
					@include align-middle;
					height: 100%;
					line-height: 1.2;
					font: {
						size: 20px;
						weight: 700;
					}
				}
			}
			&-1{
				margin-bottom: 39px;
				& > p{
					color: #1f8bcc;
					border-color: #8fc5e4;
				}
			}

			&-2{
				margin-bottom: 35px;
				& > p{
					color: #94ac35;
					border-color: #c0d377;
				}
			}
			.join-step{
				float: left;
				margin-left: 50px;
				padding-top: 30px;
				li{
					margin-top: 10px;
					line-height: 1.5;
					font-size: 16px;
					color: #333;
					&:first-child{
						margin-top: 0;
					}
					strong{
						display: inline-block;
						width: 75px;
						font-weight: 700;
					}

				}
			}
		}
		.btn-group{
			text-align: right;
			.btn{
				padding: 0 20px;
			}
		}
	}
	&-auth{
		& > .inner{
			position: relative;
			max-width: 880px;
			margin: 0 auto;
			padding-top: 60px;
		}
		.heading{
			small{
				margin-left: 15px;
				font-size: 14px;
				color: #888;
			}
		}
		.btn{
			padding: 0 20px;
		}
	}
}



